* {
  margin: 0;
}

@font-face {
    font-family: 'header';
    src: url(/memory-card/static/media/Caveat-VariableFont_wght.7519ccd6.ttf);
}

.header {
    background-color: rgb(63, 63, 63);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    padding: 16px;
}

.header-title {
    font-family: 'header', sans-serif;
    font-size: 80px;
    font-weight: bold;
}

.header-description {
    font-size: 24px;
}
body {
    background-color: #eee;
}

.scoreboard {
    margin: 16px;
    font-weight: bold;
    font-size: 20px;
}

.restart {
    border: none;
    border-radius: 8px;
    background-color: rgb(85, 85, 85);
    color: white;
    font-weight: bold;
    font-size: 20px;
    margin: 16px;
    padding: 8px 16px;
    cursor: pointer;
}

.restart:hover {
    background-color: rgb(55, 55, 55);
}

.card {
    height: 200px;
    width: auto;
    cursor: pointer;
}

.card:hover {
    -webkit-transform: scale(1.05,1.05);
            transform: scale(1.05,1.05);
}

.card-display {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    flex-shrink: 0;
    flex-wrap: wrap;
    margin: 10px;
}

.number-input {
    width: 80px;
    border-radius: 6px;
    padding: 8px 12px;
    font-size: 16px;
}

.number-input-button {
    font-weight: bold;
    font-size: 16px;
    padding: 8px 12px;
    border: none;
    border-radius: 6px;
    background-color: rgb(85, 85, 85);
    color: white;
    cursor: pointer;
}

.number-input-button:hover {
    background-color: rgb(55, 55, 55);
}

.card-amount-info,
form {
    margin: 4px 16px;
    font-size: 18px;
}

form {
    display: flex;
    grid-gap: 16px;
    gap: 16px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
